





















import api from '../../common/api/index.js'
export default {
	data() {
		return {
			title: 'map',
			latitude: 23.330154,
			longitude: 116.150986,
			covers: [{
				latitude: 23.330154,
				longitude: 116.150986,
				iconPath: '../../static/location.png',
				width: 30,
				height: 30,
				title: "荣兴木业",
				label: {
					content: "荣兴木业",
					fontSize: 12
				}
			}],
			name: null,
			address: null,
			homephone:null,
			telephone:null,
		}
	},
	computed: {
		info() {
			let self = this
			return [{
					tag: "公司名称",
					name: self.name
				},
				{
					tag: "地址",
					name: self.address,
					isAddress: true
				},
				{
					tag: "座机",
					name: self.homephone,
					isCall: true
				},
				{
					tag: "手机",
					name: self.telephone,
					isCall: true
				}
			]
		}
	},
	mounted() {
		this.queryData()
	},
	methods: {
		queryData: async function() {
			let info = uni.getStorageSync('info')
			console.log('storage info:',info)
			if(info){
				let {
					name,
					address,
					homephone,
					telephone,
					about,
					job,
					id
				} = info;
				this.name = name
				this.address = address
				this.homephone = homephone
				this.telephone = telephone
				this.content = about
				this.content1 = job
				this.id = id
			}
		},
		openMap(latitude, longitude) {
			let self = this
			uni.openLocation({
				latitude,
				longitude,
				name: self.name,
				address: self.address,
				success: function() {
					console.log('success');
				}
			});
		},
		call(obj) {
			let self = this
			if (obj.isCall) {
				uni.makePhoneCall({
					phoneNumber: obj.name
				});
			}
			if (obj.isAddress) {
				this.openMap(self.latitude, self.longitude)
			}
		}
	}
}
